import { Component, SpriteFrame, _decorator } from "cc";
import { AS } from "db://as_framework/scripts//ASComponent";
const { ccclass, property } = _decorator;

@ccclass("StoryPanel")
export class StoryPanel extends AS(Component) {
  @property({ type: SpriteFrame }) panelSlots: SpriteFrame[] = [];

  private _filled = [];

  awake() {
    this.panelSlots.forEach((data, i) => {
      this._filled[i] = false;
    });
  }
}
